import { Component, OnInit, Input, Output, EventEmitter, ElementRef } from '@angular/core';

@Component({
    selector: 'sml-input-tag',
    template: `
    <div class="position-relative">
        <textarea row="3"  id="{{name}}" class="cus-textarea" placeholder="Type and press enter" name="{{name}}" (keyup.enter)="onEnterClick()"  [(ngModel)]="value" ></textarea>
    </div>
    `,
    styles: [`
        .position-relative {
            position: relative;
        }
        .cus-textarea {
            width: 100% !important;
            padding-left: 7px;
        }
    `]
})
export class TagInputComponent implements OnInit {

    @Input() value: any = "";
    @Input() name: any = "sml-input-type";
    @Output() onEnter = new EventEmitter;

    constructor(private elementRef: ElementRef) { }
    ngOnInit() { }
    ngOnChanges(event) { }

    onEnterClick() {
        if (this.value != "") {
            let array = this.value.split(",");
            this.onEnter.emit(array);
            this.value = "";
        }
    }
}
