import { Component, Input, OnInit, EventEmitter, Output, ElementRef } from '@angular/core';

@Component({
    selector: 'sml-input-colorpicker',
    template: `
  <div class="color-picker position-relative">
  <div class="input-field" 
       [ngClass]="{'float-group':switchStyle,'float-groupdd': !switchStyle,'signal-line': signalStyle ,'float-group-static': staticStyle}"
       [ngStyle]="borderStyle"
       >
    <input 
        type="text" 
        name="color" 
        class="form-control"
        [ngStyle]="valueStyle"
        [value]="color"
        required>
        <span (click)="toggleColors()" class="icon"  [style.background]="color"></span>
    <label *ngIf="isLabel" 
          [ngStyle]="style" 
          class="label">
          {{label}}
    </label>
  </div>
  <div *ngIf="show" class="opened">
    <div class="colors">
      <div (click)="changeColor(paint)" *ngFor="let paint of defaultColors" class="circle"
      [ngStyle]="{'background': paint}"></div>
    </div> 
  </div>
  </div>
  `,
    styles: [`
  :host {
    --c-width: 100%;
    --c-height: 25px;
    --label-left: 10px;
    --label-top: 0px;
    --value-left: 10px;
    --value-top: 0px;
}
.color-picker .circle {
    height: 19px;
    width: 19px;
    box-sizing: border-box;
    border-radius: 4px;
    border: 1px solid #fff;
    margin: 0;
    padding: 0;
    cursor: pointer;
}
.color-picker .opened {
    box-shadow: 0 7px 13px 0 rgba(86, 95, 102, 0.07);
    background: #fff;
    max-width: 164px;
    padding: 1px;
    z-index: 99;
    border-radius: 5px;
    position: absolute;
    margin-top: 5px;
    right: 0px;
}
.color-picker .opened .colors {
    display: flex;
    flex-wrap: wrap;
}
.color-picker .opened .colors .circle {
    flex: 20px 0 0;
}
.color-picker .opened .colors .circle:nth-child(5n) {
    margin-right: 0;
}
.color-picker .opened .hex-code {
    display: flex;
    margin-top: 20px;
}
.color-picker .opened .hex-code p {
    width: 50%;
    text-align: center;
    font-size: 10px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    line-height: 45px;
}
.color-picker .opened .hex-code .g-input {
    border: 1px solid #e8ebed;
    height: 45px;
    border-radius: 5px;
    width: 120px;
}
.color-picker .opened .hex-code .g-input input {
    border: none;
    width: 100%;
    text-transform: uppercase;
    outline: none;
    text-align: center;
    letter-spacing: 1.5px;
    color: #595b65;
    height: 100%;
    border-radius: 2px;
    margin: 0;
    padding: 0;
}
.color-picker .colorpicker {
    position: relative;
    border-radius: 3px;
    width: 100%;
    border: 1px solid #c3c0c0;
}
.color-picker .icon {
    position: absolute;
    background: #ccc;
    width: 20px;
    height: 20px;
    top: 50%;
    transform: translateY(-50%);
    right: 9px;
    border-radius: 3px;
}
.color-picker .text-black-lighter {
    color: #404040 !important;
}
/* box css */
.position-relative {
    position: relative;
    width: var(--c-width) !important;
}
.input-field {
    border: 1px solid #dee2e6;
    border-radius: 2px;
    min-height: var(--c-height) !important;
    height: auto;
}
/* Control Css */
.form-control {
    display: block;
    padding-top: 0.375rem;
    padding-right: 0.75rem;
    padding-bottom: 0.375rem;
    padding-left: var(--value-left);
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: none;
    min-height: var(--c-height) !important;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}
.form-control {
    background-color: inherit;
    box-shadow: none !important;
}
.signal-line{
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    border-radius: 0px !important;
}
/* label css */
.label{
    position: absolute; 
    top: -24px;
    left: 0px; 
    font-size: 12px;
    padding: 0px !important;
    text-align: left;
}
.float-group {
    position: relative;
    padding: 14px 24px 2px 0px;
}
.float-group .form-control {
    padding-top: var(--value-top);
    padding-right: 0px;
    padding-bottom: 0px;
    padding-left: var(--value-left);
}
.float-group .label {
    font-size: 12px;
    font-weight: normal;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: var(--label-left);
    transition: all 200ms;
    opacity: 1;
    margin: 0;
    width: 87%;
    height: auto;
}
.float-group-static .label {
    font-size: 9px;
    font-weight: bold !important;  /* floating lable always bold */
    position: absolute;
    top: var(--label-top);
    left: var(--label-left);
    transform: translateY(0px);
    transition: all 200ms;
    opacity: 1;
    margin: 0;
    width: 87%;
    height: auto;
}
.float-group .form-control:focus {
    outline: none !important;
    box-shadow: none;
}
.float-group .form-control:focus-within ~ .label,.float-group .form-control:valid ~ .label {
    font-size: 9px;
    font-weight: bold !important;  /* floating lable always bold */
    position: absolute;
    top: var(--label-top);
    left: var(--label-left);
    transform: translateY(0px);
    transition: all 200ms;
    opacity: 1;
    margin: 0;
    width: 87%;
    height: auto;
}
`]
})
export class ColorPickerComponent {
    @Input() heading: string;
    @Input() color: string = '#d6d6d6';
    @Input() isLabel: any = false;
    @Input() switchStyle: any;
    @Input() label: string;
    @Input() style: any;
    @Input() valueStyle: any;
    @Input() signalStyle: boolean;
    @Input() staticStyle: boolean = false;
    @Input() borderStyle: any;
    @Output() event: EventEmitter<string> = new EventEmitter<string>();
    @Output() selectedColor = new EventEmitter();

    public show = false;
    public defaultColors: string[] = [
        '#000000', '#424242', '#636363', '#9C9C94', '#CEC6CE', '#EFEFEF', '#F7F7F7', '#FFFFFF',
        '#FF0000', '#FF9C00', '#FFFF00', '#00FF00', '#00FFFF', '#0000FF', '#9C00FF', '#FF00FF',
        '#F7C6CE', '#FFE7CE', '#FFEFC6', '#D6EFD6', '#CEDEE7', '#CEE7F7', '#D6D6E7', '#E7D6DE',
        '#E79C9C', '#FFC69C', '#FFE79C', '#B5D6A5', '#A5C6CE', '#9CC6EF', '#B5A5D6', '#D6A5BD',
        '#E76363', '#F7AD6B', '#FFD663', '#94BD7B', '#73A5AD', '#6BADDE', '#8C7BC6', '#C67BA5',
        '#CE0000', '#E79439', '#EFC631', '#6BA54A', '#4A7B8C', '#3984C6', '#634AA5', '#A54A7B',
        '#9C0000', '#B56308', '#BD9400', '#397B21', '#104A5A', '#085294', '#311873', '#731842',
        '#630000', '#7B3900', '#846300', '#295218', '#083139', '#003163', '#21104A', '#4A1031'
    ];

    constructor(private elementRef: ElementRef) {
    }
    ngOnChanges(event) {
        if (event.style != undefined && event.style.currentValue != undefined) {
            let css = event.style.currentValue;
            this.elementRef.nativeElement.style.setProperty('--c-width', css['c-width']);
            this.elementRef.nativeElement.style.setProperty('--c-height', css['c-height']);
            this.elementRef.nativeElement.style.setProperty('--label-left', css['label-left']);
            this.elementRef.nativeElement.style.setProperty('--label-top', css['label-top']);
        }
        if (event.valueStyle != undefined && event.valueStyle.currentValue != undefined) {
            let css = event.valueStyle.currentValue;
            this.elementRef.nativeElement.style.setProperty('--value-left', css['value-left']);
            this.elementRef.nativeElement.style.setProperty('--value-top', css['value-top']);
        }
    }
    public styleObject(): Object {
        let nType = {};
        Object.assign(nType, this.valueStyle);
        Object.assign(nType, this.borderStyle);
        return nType;
    }
    public changeColor(color: string): void {
        this.color = color;
        this.event.emit(this.color);
        this.selectedColor.emit(this.color)
        this.show = false;
    }
    public changeColorManual(color: string): void {
        const isValid = /(^#[0-9A-F]{6}$)|(^#[0-9A-F]{3}$)/i.test(color);

        if (isValid) {
            this.color = color;
            this.event.emit(this.color);
            this.selectedColor.emit(this.color);
        }
    }
    public toggleColors(): void {
        this.show = !this.show;
    }
}
