import { Component, OnInit, Input, Output, ElementRef, EventEmitter } from '@angular/core';

@Component({
    selector: 'sml-input-textarea',
    template: `
  <div class="position-relative">
  <div class="input-field" 
      [ngStyle]="borderStyle" 
      [ngClass]="{'float-group':switchStyle,'float-groupdd': !switchStyle,'signal-line': signalStyle,'float-group-static': staticStyle}">
      <textarea *ngIf="!readOnly"
      type="text" 
      class="form-control" 
      [value]="value"  
      [rows]="row" 
      [cols]="column"
      (keyup)="getValue($event)" 
      (blur)="onBlur($event)"
      [ngStyle]="valueStyle"
      required></textarea>
      <textarea *ngIf="readOnly"
      type="text" 
      class="form-control" 
      [value]="value"  
      [rows]="row" 
      [cols]="column"
      (keyup)="getValue($event)" 
      (blur)="onBlur($event)"
      [ngStyle]="valueStyle"
      required readOnly></textarea>
    <label class="label" 
          [ngStyle]="style">
          {{ label }} <span *ngIf="maxLenght">: {{ maxLenght }}</span>
    </label>
  </div>
</div>
  `,
    styles: [`
  /* css variable */
  :host {
      --c-width: 645px;
      --label-left: 10px;
      --label-top: 0px;
      --value-left: 10px;
      --value-top: 0px;
  }
  /* box css */
  .position-relative {
      position: relative;
  }
  .input-field {
      border: 1px solid rgb(222, 226, 230);
      border-radius: 2px;
      width: var(--c-width) !important;
  }
  /* control css */
  .form-control {
      display: block;
      padding-top: 0.375rem;
      padding-right: 0.75rem;
      padding-bottom: 0.375rem;
      padding-left: var(--value-left);
      font-size: 1rem;
      line-height: 1.5;
      color: #495057;
      background-color: #fff;
      background-clip: padding-box;
      border: none;
      resize: none;
      transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  }
  .form-control {
      background-color: inherit;
      box-shadow: none !important;
  }
  .signal-line{
      border-top: none !important;
      border-left: none !important;
      border-right: none !important;
      border-radius: 0px !important;
  }
  /* labelcss */
  .label {
      position: absolute;
      top: -24px;
      left: 0px; 
      font-size: 12px;
      padding: 0px !important;
        text-align: left;
  }
  .float-group {
      position: relative;
      padding: 14px 24px 2px 0px;
  }
  .float-group .form-control {
      padding-top: var(--value-top);
      padding-right: 0px;
      padding-bottom: 0px;
      padding-left: var(--value-left);
  }
  .float-group .label {
    font-size: 12px;
    font-weight: normal;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: var(--label-left);
    transition: all 200ms;
    opacity: 1;
    margin: 0;
    width: 87%;
    height: auto;
  }

  @media all and (-ms-high-contrast:none) {
      *::-ms-backdrop, .float-group .label {
          top: 0%!important;
          transform: translateY(50%);
      }
  }
  
  .float-group .form-control:focus {
      box-shadow: none;
      border: none
  }
  .float-group-static .label {
      font-size: 9px;
      font-weight: bold !important;  /* floating lable always bold */
      position: absolute;
      top: var(--label-top);
      left: var(--label-left);
      transform: translateY(0px);
      transition: all 200ms;
      opacity: 1;
      margin: 0;
      width: 87%;
      height: auto;
   }
  .float-group .form-control:focus-within ~ .label,
  .float-group .form-control:valid ~ .label {
      font-size: 9px;
      font-weight: bold !important;  /* floating lable always bold */
      position: absolute;
      top: var(--label-top);
      left: var(--label-left);
      transform: translateY(0px);
      transition: all 200ms;
      opacity: 1;
      margin: 0;
      width: 87%;
      height: auto;
  }  
  `]
})
export class TextareaComponent implements OnInit {
    @Input() value;
    @Input() name;
    @Input() row;
    @Input() column;
    @Input() label: string;
    @Input() maxLenght: any;
    @Input() switchStyle: boolean;
    @Input() style: any;
    @Input() borderStyle: any;
    @Input() signalStyle: boolean;
    @Input() staticStyle: boolean = false;
    @Input() valueStyle: any;
    @Input() inputStyle: any;
    @Input() readOnly: boolean = false;
    @Output() getvalue = new EventEmitter;
	@Output() onblur = new EventEmitter;

    constructor(private elementRef: ElementRef) { }
    ngOnInit() { }
    ngOnChanges(event) {
        if (event.style != undefined && event.style.currentValue != undefined) {
            let css = event.style.currentValue;
            this.elementRef.nativeElement.style.setProperty('--c-width', css['c-width']);
            this.elementRef.nativeElement.style.setProperty('--c-height', css['c-height']);
            this.elementRef.nativeElement.style.setProperty('--label-left', css['label-left']);
            this.elementRef.nativeElement.style.setProperty('--label-top', css['label-top']);
        }
        if (event.valueStyle != undefined && event.valueStyle.currentValue != undefined) {
            let css = event.valueStyle.currentValue;
            this.elementRef.nativeElement.style.setProperty('--value-left', css['value-left']);
            this.elementRef.nativeElement.style.setProperty('--value-top', css['value-top']);
        }
    }
    getValue(event) {
        this.getvalue.emit(event.target.value);
    }
	onBlur(event) {
        this.onblur.emit(event.target.value);
	}
}
