import { Component, OnInit, Input, Output, EventEmitter, ElementRef } from '@angular/core';

@Component({
    selector: 'sml-input-updown',
    template: `
  <div class="position-relative">
  <div class="input-field" 
    [ngClass]="{'float-group':switchStyle,'float-groupdd': !switchStyle,'signal-line': signalStyle,'float-group-static': staticStyle}"
    [ngStyle]="borderStyle">
    <input 
        type="number" 
        [(ngModel)]="initialValue" 
        [ngStyle]="valueStyle"
        (blur)="onBlurMethod()"
        [style.text-align]="textAlign"
        class="form-control" required>
    <div class="numberSpinner">
      <a href="javascript:void(0)" (click)="increaseValue()" class="spinnerBtn">
        <i class="fa fa-caret-up"></i>
      </a>
      <a href="javascript:void(0)" (click)="decreaseValue()" class="spinnerBtn">
        <i class="fa fa-caret-down"></i>
      </a>
    </div>
    <label *ngIf="isLabel" [ngStyle]="style" class="label">{{label}}</label>
  </div>
</div>
  `,
    styles: [`
  /* css variable */
  :host {
      --c-width: 100%;
      --c-height: 25px;
      --label-left: 10px;
      --label-top: 0px;
      --value-left: 10px;
      --value-top: 0px;
  }
  /* box css */
  .position-relative {
      position: relative;
      width: var(--c-width) !important;
  }
  .input-field {
      border: 1px solid #dee2e6;
      border-radius: 2px;
      min-height: var(--c-height) !important;
      height: auto;
  }
  /* control css */
  .form-control {
      display: block;
      padding-top: 0.375rem;
      padding-right: var(--value-left);
      padding-bottom: 0.375rem;
      padding-left: 0.75rem;
      font-size: 1rem;
      line-height: 1.5;
      color: #495057;
      background-color: #fff;
      background-clip: padding-box;
      border: none;
      text-align: right;
      min-height: var(--c-height) !important;
      transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  }
  .form-control {
      background-color: inherit;
      box-shadow: none !important;
  }
  .signal-line{
      border-top: none !important;
      border-left: none !important;
      border-right: none !important;
      border-radius: 0px !important;
  }
  /* labelcss */
  .label{
      position: absolute; 
      top: -24px;
      left: 0px; 
      font-size: 12px;
      padding: 0px !important;
        text-align: left;
  }
  .float-group {
      position: relative;
      padding: 14px 7px 2px 0px;
  }
  .float-group .form-control {
      padding-top: var(--value-top);
      padding-right: var(--value-left);
      padding-bottom: 0px;
      padding-left: var(--value-left);
  }
  .float-group .label {
      font-size: 12px;
      font-weight: normal;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: var(--label-left);
      transition: all 200ms;
      opacity: 1;
      margin: 0;
      width: 87%;
      height: auto;
  }
  .float-group .form-control:focus {
      outline: none !important;
      box-shadow: none;
  }
  .float-group-static .label {
    font-size: 9px;
      font-weight: bold !important;  /* floating lable always bold */
      position: absolute;
      top: var(--label-top);
      left: var(--label-left);
      transform: translateY(0px);
      transition: all 200ms;
      opacity: 1;
      margin: 0;
      width: 87%;
      height: auto;
  }
  .float-group .form-control:focus-within ~ .label,.float-group .form-control:valid ~ .label {
      font-size: 9px;
      font-weight: bold !important;  /* floating lable always bold */
      position: absolute;
      top: var(--label-top);
      left: var(--label-left);
      transform: translateY(0px);
      transition: all 200ms;
      opacity: 1;
      margin: 0;
      width: 87%;
      height: auto;
  }
  
  .numberSpinner {
      display: inline-flex;
      flex-direction: column;
      position: absolute;
      right: 0px;
      top: 0px;
      bottom: 0px;
      border-top-right-radius: 2px;
      border-bottom-right-radius: 2px;
      overflow: hidden;
      z-index: 1;
  }
  .spinnerBtn {
      background: #f1f1f1;
      border: none;
      font-size: 14px;
      color: initial;
      padding: 0 7px;
      display: flex;
      height: 50%;
      align-items: center;
      justify-content: center;
  }
  .spinnerBtn:active {
      color: #fff;
      background: #666666!important;
  }
  .spinnerBtn:hover {
      background: #d1d1d1;
      text-decoration: none;
  }
  input[type=number]::-webkit-inner-spin-button{
      opacity: 0;
      font-size: 14px
  } 
  `]
})
export class UpdownComponent implements OnInit {
    @Input() initialValue: any = 0;
    @Input() switchStyle: any;
    @Input() isNagative: any = false;
    @Input() textAlign: any = 'right';
    @Input() label: string;
    @Input() isLabel: any = false;
    @Input() style: any;
    @Input() valueStyle: any;
    @Input() borderStyle: any;
    @Input() signalStyle: boolean;
    @Input() staticStyle: boolean = false;
    @Output() valueChange = new EventEmitter();

    constructor(private elementRef: ElementRef) {
    }

    ngOnInit() {
        if (this.initialValue == undefined || this.initialValue == "" || this.initialValue == null) {
            this.initialValue = 0;
        } else {
            this.initialValue = parseInt(this.initialValue);
        }
    }
    ngOnChanges(event) {
        if (event.style != undefined && event.style.currentValue != undefined) {
            let css = event.style.currentValue;
            this.elementRef.nativeElement.style.setProperty('--c-width', css['c-width']);
            this.elementRef.nativeElement.style.setProperty('--c-height', css['c-height']);
            this.elementRef.nativeElement.style.setProperty('--label-left', css['label-left']);
            this.elementRef.nativeElement.style.setProperty('--label-top', css['label-top']);
        }
        if (event.valueStyle != undefined && event.valueStyle.currentValue != undefined) {
            let css = event.valueStyle.currentValue;
            this.elementRef.nativeElement.style.setProperty('--value-left', css['value-left']);
            this.elementRef.nativeElement.style.setProperty('--value-top', css['value-top']);
        }
    }

    public styleObject(): Object {
        let nType = {};
        Object.assign(nType, this.valueStyle);
        Object.assign(nType, this.borderStyle);
        return nType;
    }

    increaseValue() {
        if (this.initialValue == undefined || this.initialValue == "" || this.initialValue == null) {
            this.initialValue = 0;
        }
        this.initialValue += 1;
        this.valueChange.emit(this.initialValue);
    }

    decreaseValue() {
        if (this.initialValue == undefined || this.initialValue == "" || this.initialValue == null) {
            this.initialValue = 0;
        }
        if (this.initialValue > 0) {
            this.initialValue -= 1;
            this.valueChange.emit(this.initialValue);
        }
        if (this.isNagative) {
            this.initialValue -= 1;
            this.valueChange.emit(this.initialValue);
        }
        return false;
    }
    onBlurMethod() {
        this.valueChange.emit(this.initialValue);
    }
}
